import { Controller } from '@ali/4ever-cangjie';
import { ViewMark } from '../models/marks';

export default function cancelComment(controller: Controller) {
  const { decorations } = controller.value;
  const activeDecoration = decorations.find(
    (d) => ViewMark.isViewMark(d.mark) && d.mark.data.isNew,
  );
  if (activeDecoration) {
    controller.setDecorations(
      decorations.filter((d) => d !== activeDecoration),
    );
  }
}
